import { Injectable } from '@angular/core';
import { Headers, Http } from '@angular/http';
import { Router } from '@angular/router';

@Injectable()
export class TaskService {
  item: any;

  constructor(
    private http: Http,
    private router: Router
  ) { }

  list(task: any) {
    return this.http.get(`${process.env.API}/tasks/${task._id}`)
      .toPromise()
      .then((res: any) => {
        return JSON.parse(res._body);
      })
      .catch((err: any) => {
        console.log(JSON.stringify(err));
      });
  }

  addIteration(task: any, description: string) {
    const iteration = {
      description: description,
      startTime: new Date()
    }
    return this.http.post(`${process.env.API}/tasks/${task._id}/iterations`, iteration)
      .toPromise()
      .then((res: any) => {
        return JSON.parse(res._body);
      })
      .catch(e => {
        console.log(e);
      });
  }

  removeIteration(task: any, iteration: any) {
    return this.http.delete(`${process.env.API}/tasks/${task._id}/iterations/${iteration._id}`)
      .toPromise()
      .catch(err => {
        return;
      });
  }

  changeTitle(task: any) {
    const changedTask = {
      name: task.name
    };
    this.http.put(`${process.env.API}/tasks/${task._id}`, changedTask)
      .toPromise()
      .then((res: any) => {
        // changed task JSON.parse(res._body);
      })
      .catch((err: any) => {
        return;
      });
  }

  changeDescription(task: any) {
    const changedTask = {
      descriptions: task.descriptions
    };
    return this.http.put(`${process.env.API}/tasks/${task._id}`, changedTask)
      .toPromise()
      .then((res: any) => {
        // changed task JSON.parse(res._body);
      })
      .catch((err: any) => {
        return;
      });
  }

}



