import { ROUTER_DIRECTIVES, Router } from '@angular/router';
import { Component, Injectable } from '@angular/core';

import { AuthService } from './service';
import { User } from './user';

@Injectable()
@Component({
  selector: 'auth',
  templateUrl: './src/components/auth/auth.html',
  styleUrls: ['./src/components/auth/auth.less'],
  directives: [
    ROUTER_DIRECTIVES
  ],
  providers: [
    AuthService
  ]
})
export class AuthComponent {

  authswitcher: boolean;
  success: boolean;
  bad: boolean;
  dontMatch = false;
  duplicate = false;
  noUser = false;

  loguser = {
    username: '',
    password: ''
  };

  reguser = {
    username: '',
    password: '',
    confpassword: ''
  };

  constructor(
    private _AuthService: AuthService,
    private _Router: Router
  ) { }

  login(user: User) {
    this._AuthService.login(user).then((res: any) => {
      if (res === 'Unauthorized') {
        this.loguser.username = '';
        this.loguser.password = '';
        this.noUser = true;
        return setTimeout(() => {
          this.noUser = false;
        }, 3000);
      }
      if (res === 'Bad Request') {
        // form is empty, default popup
        return;
      }
      this._Router.navigate(['dashboard/overview']);
    })
  }

  register(user: User) {
    this._AuthService.register(user).then((res: any) => {
      if (res == 'duplicate') {
        this.duplicate = true;
        setTimeout(() => {
          this.duplicate = false;
        }, 3000);
      }
      if (res == 'success') {
        this.success = true;
        this.loguser.username = this.reguser.username;
        this.loguser.password = this.reguser.password;
        setTimeout(() => {
          this.authswitcher = false;
        }, 3000);
      }
    });
  }

  confirmPassAndConfPass() {
    setTimeout(() => {
      if (this.reguser.password != this.reguser.confpassword) {
        this.bad = true;
      } else {
        this.bad = false;
      }
    }, 100);
  }
}



