import { Component, OnInit, Output } from '@angular/core';
import { Injectable } from '@angular/core';
import { ROUTER_DIRECTIVES } from '@angular/router'; // ?
import { Router } from '@angular/router';

import { DashboardService } from '../dashboard/service';
import { IterationService } from '../iteration/service';
import { TaskListService } from './service';
import { TaskComponent } from '../task';
import { OrderByPipe } from './sortByDatePipe';

@Injectable()
@Component({
  selector: 'taskList',
  templateUrl: './src/components/taskList/taskList.html',
  styleUrls: ['./src/components/taskList/taskList.less'],
  directives: [
    ROUTER_DIRECTIVES,
    TaskComponent
  ],
  providers: [
    IterationService,
    DashboardService,
    TaskListService
  ],
  pipes: [OrderByPipe]
})
export class TaskListComponent implements OnInit {
  @Output() items: any;

  userName: string;
  noTasks: boolean;
  addnewtask: any;

  constructor(
    private _TaskListService: TaskListService,
    private _DashboardService: DashboardService,
    private _IterationService: IterationService
  ) { }

  ngOnInit() {
    this.getUserName();
    this.list();
  }

  list() {
    this._TaskListService.list().then((res: any) => {
      this.items = res;
      if (this.items.length === 0) {
        this.noTasks = true;
      } else {
        this.noTasks = false;
      }
    });
  }

  addNewTask(name: string, descriptions: string) {
    this.addnewtask = !this.addnewtask;
    this._TaskListService.addNewTask(name, descriptions).then((newTask: any) => {
      this.items.push(newTask);
    });
    return '';
  }

  removeTask(task: any) {
    return this._TaskListService.removeTask(task).then((res: any) => {
      const indexOfTask = this.items.findIndex((item: any) => {
        return task._id === item._id;
      });
      this.items.splice(indexOfTask, 1);
    });
  }

  logout() {
    this._DashboardService.logout();
  }

  getUserName() {
    this._DashboardService.getUserName().then((res: any) => {
      this.userName = res;
    });
  }

}



