import { Injectable, Output, EventEmitter } from '@angular/core';
import { Headers, Http } from '@angular/http';
import { Router } from '@angular/router';

@Injectable()
export class TaskListService {
  userId: any;
  items: any;

  constructor(
    private http: Http,
    private router: Router
  ) { }

  list() {
    return this.http.get(`${process.env.API}/users/current`)
      .toPromise()
      .then((res: any) => {
        this.userId = JSON.parse(res._body).userId;
        return this.getTaskOfCurrentUser(this.userId);
      })
      .catch((err: any) => {
        console.log('Err: ' + JSON.stringify(err._body));
      });
  }

  getTaskOfCurrentUser(id: string) {
    return this.http.get(`${process.env.API}/tasks/?userId=${id}`)
      .toPromise()
      .then((res: any) => {
        return this.items = JSON.parse(res._body);
      })
  }

  logout() {
    this.http.get(`${process.env.API}/logout`)
      .toPromise()
      .then((res: any) => {
        console.log(res.data.message);
      })
      .catch((err: any) => {
        console.log(JSON.stringify(err));
      });
  }

  addNewTask(name: string, descriptions: string) {
    const userId = this.userId;
    const newTask = {
      userId,
      name,
      descriptions,
      createDate: new Date()
    };

    return this.http.post(`${process.env.API}/tasks`, newTask)
      .toPromise()
      .then((res: any) => {
        return (JSON.parse(res._body));
      })
      .catch((err: any) => {
        console.log(JSON.stringify(err));
      });
  }

  removeTask(task: any) {
    return this.http.delete(`${process.env.API}/tasks/${task._id}`)
      .toPromise()
      .catch((err: any) => {
        console.log(JSON.stringify(err));
      });
  }
}



