import { Component, Input, OnInit, Inject, forwardRef, Injectable } from '@angular/core';
import { ROUTER_DIRECTIVES } from '@angular/router';
import { Observable } from 'rxjs/Rx';

import { TaskListService } from '../taskList/service';
import { TaskService } from '../task/service';
import { IterationService } from './service';
import { TaskComponent } from '../task';

@Injectable()
@Component({
  selector: 'iteration',
  templateUrl: './src/components/iteration/iteration.html',
  styleUrls: ['./src/components/iteration/iteration.less'],
  directives: [
    ROUTER_DIRECTIVES
  ],
  providers: [
    IterationService,
    TaskListService
  ]
})
export class IterationComponent implements OnInit {
  @Input() iter: any;
  @Input() task: any;

  delayForSending: boolean;
  itemStartTime: any;
  currentDate: any;
  totalHour: any;
  totalMin: any;
  totalSec: any;
  diffTime: any;
  fin: any;
  taskRemoveMenu: boolean;

  constructor(
    @Inject(forwardRef(() => TaskComponent)) private _TaskComponent: TaskComponent,
    private _IterationService: IterationService,
    private _TaskService: TaskService,
    private _TaskListService: TaskListService
  ) { }

  ngOnInit() {
    this.checkCurrentTime(this.iter.finishTime);
  }

  checkCurrentTime(finishTime: any) {
    if (finishTime) return;

    Observable.interval(1000).subscribe(() => {

      this.currentDate = new Date();

      const sec = 1000;
      const min = 1000 * 60;
      const hour = 1000 * 60 * 60;

      this.itemStartTime = new Date(this.iter.startTime);

      this.totalHour = Math.floor((this.currentDate - this.itemStartTime) / hour);
      this.totalMin = Math.floor((this.currentDate - this.itemStartTime) / min) % 60;
      this.totalSec = Math.floor((this.currentDate - this.itemStartTime) / sec) % 60;

      if (this.totalHour.toString().length == 1) {
        this.totalHour = '0' + this.totalHour;
      }
      if (this.totalMin.toString().length == 1) {
        this.totalMin = '0' + this.totalMin;
      }
      if (this.totalSec.toString().length == 1) {
        this.totalSec = '0' + this.totalSec;
      }
      this.diffTime = this.totalHour + ':' + this.totalMin + ':' + this.totalSec;
    });
  }

  getFinishTime(task: any, iteration: any) {
    this._IterationService.getFinishTime(task, iteration).then((res: any) => {
      this.iter = res;
    });
  }

  removeIteration(task: any, iteration: any) {
    this.delayForSending = true;
    this._TaskComponent.removeIteration(task, iteration).then(() => {
      this.delayForSending = false;
    });
  }

  calculateTime(task: any, iteration: any) {
    return this._IterationService.calculateTime(task, iteration);
  }
}



