import { Injectable } from '@angular/core';
import { Headers, Http } from '@angular/http';

import { User } from '../auth/user';

@Injectable()
export class DashboardService {

  constructor(private http: Http) {
  }

  getUserName() {
    return this.http.get(`${process.env.API}/users/current`)
      .toPromise()
      .then((res: any) => {
        return JSON.parse(res._body).userName;
      })
      .catch(e => {
        return;
      });
  }

  logout() {
    return this.http.get(`${process.env.API}/logout`)
      .toPromise()
      .then((res: any) => {
        return;
      })
      .catch(e => {
        return;
      });
  }

}



