import { Injectable } from '@angular/core';
import { Headers, Http } from '@angular/http';

@Injectable()
export class IterationService {
  st: any;
  fin: any;

  constructor(
    private http: Http
  ) { }

  getFinishTime(task: any, iteration: any) {
    const changedIteration = {
      finishTime: new Date()
    };
    return this.http.put(`${process.env.API}/tasks/${task._id}/iterations/${iteration._id}`, changedIteration)
      .toPromise()
      .then((res: any) => {
        return JSON.parse(res._body);
      }).catch((err: any) => {
        console.log(err);
      });
  }

  calculateTime(task: any, iteration: any) {
    this.st = new Date(iteration.startTime);
    this.fin = new Date(iteration.finishTime);

    const sec = 1000;
    const min = 1000 * 60;
    const hour = 1000 * 60 * 60;

    const totalHour = Math.floor((this.fin - this.st) / hour);
    const totalMin = Math.floor((this.fin - this.st) / min) % 60;
    const totalSec = Math.floor((this.fin - this.st) / sec) % 60;

    const diff = totalHour + 'h : ' + totalMin + 'm : ' + totalSec + 's';

    return diff;
  }
}



