import { Component, OnInit, Input, Output, Inject, forwardRef } from '@angular/core';
import { Injectable } from '@angular/core';
import { ROUTER_DIRECTIVES } from '@angular/router'; // ?
import { Router } from '@angular/router';

import { TaskService } from './service';
import { TaskListService } from '../taskList/service';
import { IterationService } from '../iteration/service';
import { TaskListComponent } from '../taskList';

import { IterationComponent } from '../iteration';

@Injectable()
@Component({
  selector: 'task',
  templateUrl: './src/components/task/task.html',
  styleUrls: ['./src/components/task/task.less'],
  directives: [
    ROUTER_DIRECTIVES,
    IterationComponent
  ],
  providers: [
    TaskService
  ]
})
export class TaskComponent {
  @Input() item: any;

  description: string;
  delayForSending: boolean;
  bounceOut: boolean = false;
  active: boolean;

  constructor(
    @Inject(forwardRef(() => TaskListComponent)) private _TaskListComponent: TaskListComponent,
    private _TaskService: TaskService,
    private _TaskListService: TaskListService,
    private _IterationService: IterationService
  ) { }

  ngOnInit() {
    this.item._component = this;
    this.list();
  }

  list() {
    this._TaskService.list(this.item).then((res: any) => {
      this.item = res;
    });
  }

  removeTask(task: any) {
    this._TaskListComponent.removeTask(task).then;
  }

  changeTitle(task: any) {
    this._TaskService.changeTitle(task);
  }

  changeDescription(task: any) {
    this._TaskService.changeDescription(task);
  }

  addIteration(task: any, description: string) {
    this._TaskService.addIteration(task, description).then(iteration => {
      this.item.iterations.push(iteration);
      this.clear();
      this.delayForSending = false;
    });
  }

  removeIteration(task: any, iteration: any) {
    return this._TaskService.removeIteration(task, iteration).then((res: any) => {
      const indexOfIteration = this.item.iterations.findIndex((iter: any) => {
        return iteration._id === iter._id;
      });
      this.item.iterations.splice(indexOfIteration, 1);
    });
  }

  stopCurrentActiveIterationAndAddNewIteration(tasky: any, description: string) {
    this.delayForSending = true;
    this.stop();
    this._TaskListService.items.forEach((item: any) => {
        item._component.stop();
      });
    this.addIteration(tasky, description);
  }

  stop() {
    this.item.iterations.forEach((eachiter: any) => {
      if (eachiter.finishTime === undefined) {
        this._IterationService.getFinishTime(this.item, eachiter).then(res => {
          const indexOfIteration = this.item.iterations.findIndex((iter: any) => {
            return eachiter._id === iter._id;
          });
          this.item.iterations[indexOfIteration] = res;
        });
      }
    });
  }

  clear() {
    this.description = '';
  }
}



