import { NgModule, Injectable, provide } from '@angular/core';
import { BrowserModule  } from '@angular/platform-browser';
import { FormsModule } from '@angular/forms';
import { RouterModule } from '@angular/router'
import { BrowserXhr, HTTP_PROVIDERS } from '@angular/http';
import { MdRadioModule } from '@angular2-material/radio';

import { appRoutes, appRoutingProviders } from '../../router';

import { AppComponent } from './component.ts';

import { AuthComponent } from '../auth';
import { DashboardComponent } from '../dashboard';
import { OverviewComponent } from '../overview';
import { TaskListComponent } from '../taskList';
import { StatisticComponent } from '../statistic';
import { PageNotFoundComponent } from '../pagenotfound';

import 'font-awesome/css/font-awesome.css';

@Injectable()
class CORSBrowserXHR extends BrowserXhr {
  build(): any {
    var xhr: any = super.build();
    xhr.withCredentials = true;
    return xhr;
  }
}

@NgModule({
  declarations: [
    AppComponent,
    AuthComponent,
    DashboardComponent,
    OverviewComponent,
    TaskListComponent,
    StatisticComponent,
    PageNotFoundComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    MdRadioModule,
    appRoutes
  ],
  providers: [
    HTTP_PROVIDERS,
    provide(BrowserXhr, { useClass: CORSBrowserXHR }),
    appRoutingProviders
  ],
  bootstrap: [AppComponent],
})
export class AppModule { }



