import { Component, Injectable, Output } from '@angular/core';
import { Headers, Http } from '@angular/http';

import { User } from './user';

@Injectable()
export class AuthService {
  private user: any;

  constructor(private http: Http) {
  }

  login(user: User) {
    return this.http.post(`${process.env.API}/login`, user)
      .toPromise()
      .then((res: any) => {
        this.user = JSON.parse(res._body);
        return this.user;
      })
      .catch((e: any) => {
        return e._body;
      });
  }

  register(user: User) {
    return this.http.post(`${process.env.API}/register`, user)
      .toPromise()
      .then(res => {
        return 'success';
      })
      .catch((e: any) => {
        if (e._body.split(' ')[1] == 'duplicate') {
          return 'duplicate';
        }
      });
  }
}



