import { Component, OnInit, Input } from '@angular/core';
import { CHART_DIRECTIVES } from 'ng2-charts/ng2-charts';

const template = require('./chart.html');
const style = require('./chart.less');

@Component({
  selector: 'chart',
  directives: [CHART_DIRECTIVES],
  styles: [`
    .chart {
      display: block;
    }
  `],
  template: template
})
export class ChartsComponent implements OnInit {
  @Input() item: any;

  folded = true;

  pieChartshow = true;
  nodata: boolean;

  fin: any;
  st: any;

  datasets: any[];
  labels: any[];

  options = {
    scales:  {
      yAxes: [{
        ticks: {
          beginAtZero: true
        }
      }]
    }
  };
  ngOnInit() {
    let iterations = this.item.iterations;
    const numOfIter = iterations.length;
    if(numOfIter === 0) return this.nodata = true;

    var names: any[] = [];
    var data: any[] = [];

    for (let i = 0; i < numOfIter; i++) {
      this.st = new Date(iterations[i].startTime);
      this.fin = new Date(iterations[i].finishTime);

      if (this.fin == 'Invalid Date') {
        this.fin = new Date();
      }

      let name = iterations[i].description;
      let onlySec = Math.floor((this.fin - this.st) / 1000);

      if (!name || name == " ") name = '#' + i;

      names.push(name);
      data.push(onlySec);
    }

    this.datasets = [
      {
        label: name,
        data: data
      }
    ];

   this.labels = names;
  }
}



