import { Component, OnInit, Inject, forwardRef } from '@angular/core';
import { ROUTER_DIRECTIVES } from '@angular/router'; // ?
import { Injectable } from '@angular/core';

import { DashboardService } from '../dashboard/service';
import { TaskListService } from '../taskList/service';
import { StatisticService } from './service';
import { ChartsComponent } from '../chart';
import { OrderByPipe } from '../taskList/sortByDatePipe';

@Injectable()
@Component({
  selector: 'statistic',
  templateUrl: './src/components/statistic/statistic.html',
  styleUrls: ['./src/components/statistic/statistic.less'],
  directives: [
    ROUTER_DIRECTIVES,
    ChartsComponent
  ],
  providers: [
    DashboardService,
    StatisticService,
    TaskListService
  ],
  pipes: [OrderByPipe]
})
export class StatisticComponent implements OnInit {
  userName: string;
  noTasks: any;
  items: any;
  list: any;

  constructor(
    private _DashboardService: DashboardService,
    private _StatisticService: StatisticService,
    private _TaskListService: TaskListService
  ) { }

  ngOnInit() {
    this.getUserName();
    this.list = this._TaskListService.list().then(res => {
      this.items = res;
    });
  }

  logout() {
    this._DashboardService.logout();
  }

  getUserName() {
    this._DashboardService.getUserName().then((res: any) => {
      this.userName = res;
    });
  }
}



